@import "../../assets/scss/app";

.howMuchIs {
  padding-top: 48px;

  @include desktop() {
    padding-top: 67px;
  }

  .cards {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include desktop() {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-column-gap: 30px;
    }

    .card {
      display: flex;
      flex-direction: column;
      align-items: center;
      border-radius: 36px;

      &_light {
        box-shadow: -3px -3px 5px #FFFFFF, 0px 0px 5px #D6D6E1;

      }

      &_dark {
        box-shadow: -3px -3px 5px #30343A, 3px 3px 5px #24262C;
      }

      .icon {

      }
    }

  }

}